.sidebar {
  flex: 1;
  border: 0.5px solid lightgray;
  min-height: 100vh;
  background-color: white;
  .top {
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;

    .logo {
      font-size: 10px;
      font-weight: bold;
      color: purple;
    }
  }
  hr {
    height: 0;
    border: 0.5px solid lightgray;
  }

  .center {
    padding-left: 10px;

    ul {
      list-style: none;
      margin: 0;
      padding: 0;

      .title {
        font-size: 10px;
        font-weight: bold;
        color: #999;
        margin-top: 15px;
        margin-bottom: 5px;
      }

      li {
        display: flex;
        align-items: center;
        padding: 5px;
        cursor: pointer;

        &:hover {
          background-color: lightblue;
        }
        .icon {
          font-size: 18px;
          color: blue;
        }
        span {
          font-size: 13px;
          font-weight: 600;
          color: blue;
          margin-left: 10px;
        }
      }
    }
  }
  .bottom {
    display: flex;
    align-items: center;
    margin: 10px;
    .colorOption {
      width: 20px;
      height: 20px;
      border-radius: 5px;
      border: 1px solid #7451f8;
      cursor: pointer;
      margin: 5px;
      &:nth-child(1) {
        background-color: white;
      }
      &:nth-child(2) {
        background-color: black;
      }
    }
  }
}
// Large devices (desktops, less than 1200px)
@media (max-width: 1199.98px) {
  .sidebar {
    flex: 1;
    border: 0.5px solid lightgray;
    min-height: 100vh;
    background-color: white;
    .top {
      height: 50px;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    hr {
      height: 0;
      border: 0.5px solid lightgray;
    }

    .center {
      padding-left: 10px;

      ul {
        list-style: none;
        margin: 0;
        padding: 0;

        .title {
          font-size: 10px;
          font-weight: bold;
          color: #999;
          margin-top: 15px;
          margin-bottom: 5px;
        }

        li {
          display: flex;
          align-items: center;
          padding: 5px;
          cursor: pointer;

          &:hover {
            background-color: lightblue;
          }
          .icon {
            font-size: 18px;
            color: purple;
          }
          span {
            display: none;
          }
        }
      }
    }
    .bottom {
      display: flex;
      align-items: center;
      margin: 10px;
      .colorOption {
        width: 20px;
        height: 20px;
        border-radius: 5px;
        border: 1px solid #7451f8;
        cursor: pointer;
        margin: 5px;
        &:nth-child(1) {
          background-color: white;
        }
        &:nth-child(2) {
          background-color: black;
        }
      }
    }
  }
}
